                <img src="icons/logo-facebook2.png" width="32" alt="Facebook PetCare">
              </a>
            </li>
            <li v-if="shouldShowReservationButton">
              <a  class="button page_button_reservation" type="button" href="https://www.wettermin.pl/lecznice/gdynia/5480">
