              </a>
            </li>
          </ul>
        </div>
      
        <div class="container-fluid main-menu">
          <ul>
            <li>
              <router-link to="/">
                <font-awesome-icon icon="fa-solid fa-house" class="home-icon"/>
              </router-link>
            </li>
            <li><router-link to="/o-nas">O nas</router-link></li>
            <li><router-link to="/pacjenci">Pacjenci</router-link></li>
