<script>

import { RouterLink } from 'vue-router';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';

export default {
  name: 'Header',
  components: {
    RouterLink,
    FontAwesomeIcon
  },
  data() {
    return {
      shouldShowReservationButton: false,
      shouldShowReservationButton2: false,

    };
  },
  mounted() {
    this.checkScreenWidth();
    window.addEventListener('resize', this.setPlaceholderHeight);
    window.addEventListener('resize', this.checkScreenWidth);

    this.$nextTick(() => {
      this.setPlaceholderHeight();
    });
  
    this.$router.afterEach(() => {
      this.$nextTick(() => {
        this.setPlaceholderHeight();
      });
    });

  },
  beforeUnmount() {
    window.removeEventListener('resize', this.setPlaceholderHeight);
    window.removeEventListener('resize', this.checkScreenWidth);
  },
  methods: {
    setPlaceholderHeight() {
      if (this.$refs.topNavigation) {
        var topNavigationHeight = this.$refs.topNavigation.offsetHeight;
        this.$refs.placeholder.style.height = topNavigationHeight + 'px';
      }
    },
    checkScreenWidth() {
      const screenWidth = window.innerWidth;

      if (screenWidth > 1200) {
        this.shouldShowReservationButton = true;
        this.shouldShowReservationButton2 = false;
      } else {
        this.shouldShowReservationButton = false;
        this.shouldShowReservationButton2 = true;
      }       
    }
  }
}
</script>

