<template>
  <div>
    <h1 id="animated-header">Widzimy się w PetCare</h1>
  </div>
</template>

<script>
const texts = [
  "Widzimy się w PetCare",
  "Leczymy, aby Twój pupil mógł widzieć świat wyraźnie",
  "Przychodnia Weterynaryjna PetCare Gdynia",
  "Z nami oczy Twojego zwierzaka są bezpieczne"
];

export default {
  mounted() {
    this.type();
  },
  methods: {
    type() {
      let currentTextIndex = 0;
      let currentCharIndex = 0;
      const speed = 100;
      const pause = 2000;
      const header = document.getElementById("animated-header");

      const type = () => {
        const currentText = texts[currentTextIndex];
        const currentTextLength = currentText.length;

        if (currentCharIndex < currentTextLength) {
          header.textContent = currentText.substring(0, currentCharIndex + 1);
          currentCharIndex++;
          setTimeout(type, speed);
        } else {
          setTimeout(() => {
            currentCharIndex = 0;
            currentTextIndex = (currentTextIndex + 1) % texts.length;
            type();
          }, pause);
        }
      };

      type();
    }
  }
};
</script>

  
  <style>
  #animated-header {
    white-space: nowrap;
    overflow: hidden;
    border-right: 3px solid;
    border-color: #fff;
    animation: blink-caret 0.75s step-end infinite;
    display: inline-block;
  }
  
  @keyframes blink-caret {
    from, to {
      border-color: transparent;
    }
    50% {
      border-color: #fff;
    }
  }

  @media (max-width: 800px) {
   .powitanie h1 {
      font-size: 1.2rem;;
    }
}

@media (min-width: 801px) and (max-width: 960px){
   
  .powitanie h1 {
    font-size: 1.4rem;
  }

}

@media (min-width: 961px) and (maz-width: 1600px)  {
  .powitanie h1 {
    font-size: 1.5rem;;
  }

}

@media (min-width: 1601px) {
  .powitanie h1 {
      font-size: 1.6rem;;
    }
}


  </style>
  